<template>
  <div id="QRCode">
    <div class="code" ref="QRCode">
      <vue-qr
        :title="Name"
        :logoSrc="logo"
        :text="Content"
        :size="256"
        :margin="0"
        :logoScale="1"
        :logoMargin="3"
      />
      <div class="desc">
        <span>{{ Name }}</span>
      </div>
    </div>
    <div class="qrCodeOper">
      <el-button
        v-if="ShowDownload"
        icon="el-icon-download"
        type="primary"
        size="small"
        @click="downloadQRCode"
        ></el-button
      >
    </div>
  </div>
</template>
<script>
import VueQr from "vue-qr";
import html2canvas from "html2canvas";
import { DownloadBase64 } from "@/scripts/utils";
import logo from "@/assets/img/logo.png";
export default {
  props: {
    Content: {
      type: String,
      default: "",
    },
    Name: {
      type: String,
      default: "",
    },
    // 
    ShowDownload: {
      type: Boolean,
      default: true,
    },
  },
  data() {
    return { logo: logo };
  },
  components: {
    VueQr,
  },
  methods: {
    downloadQRCode() {
      html2canvas(this.$refs.QRCode, {
        backgroundColor: null, // 
      }).then((canvas) => {
        const dataURL = canvas.toDataURL("image/png");
        DownloadBase64(this.Name + ".png", dataURL);
      });
    },
  },
};
</script>

<style lang="less">
#QRCode {
  text-align: center;
  .code {
    margin: auto;
    display: inline-block;
    > div {
      //   margin: 10px;
    }

    > div:first-child {
      margin-bottom: 0px;
    }

    .desc {
      margin-top: -5px;
      clear: both;
      background: #fff;
      padding: 5px;
      color: #000;
      font-weight: 700;
      font-size: 18px;
      border: solid 1px #e6e6e6;
    }

    img {
      border: solid 1px #e6e6e6;
      padding: 5px;
    }
  }

  .qrCodeOper {
    margin-top: 10px;
    clear: both;
  }
}
</style>
